<script>
    import {Howl, Howler} from 'howler';
    export let src;
    let soundSrc = [];
    soundSrc.push(src);
    export let sound = new Howl({
        src: soundSrc,
        autoplay: false,
        loop: true,
        onplayerror: function() {
        sound.once('unlock', function() {
        sound.play();
        });
        }
    })
    sound.play();
    let soundState = {playing: true};
    function handleSound(){
        soundState.playing = !soundState.playing;
        if(soundState.playing === true){
            sound.play();
        }else{
            sound.pause();
        }
    }
</script>

<div class="w3-display-bottomleft">
    {#if soundState.playing}
    <button on:click={handleSound} class="playing w3-button w3-black">></button>
    {:else}
    <button on:click={handleSound} class="pause w3-button w3-black">=</button>
    {/if}
</div>

<style>
    button{
        font-size:3em;
        margin-left: 2rem;
    }
</style>